import Vue from 'vue'
import VueRouter from 'vue-router'
import MenuModel from '../components/menu'
import Install from '../docs/install.md'
import QuickStart from '../docs/quickStart.md'

Vue.use(VueRouter)
export const pages = {
  Form: [
    {
      path: 'pageDynamic',
      name: '下拉分页'
    }
  ]
}

function createRouter (moduleName, route) {
  return {
    path: `/component/${route.path}`,
    name: route.path,
    component: () => import('../docs/' + moduleName.toLowerCase() + '/' + route.path + (route.filetype || '.md'))
  }
}
export const baseRouters = () => {
  const routes = []
  for (const key in pages) {
    if (Object.hasOwnProperty.call(pages, key)) {
      const element = pages[key]
      element.forEach(page => {
        routes.push(createRouter(key, page))
      })
    }
  }
  return routes
}
const routes = [
  {
    path: '/',
    redirect: '/component/install'
  },
  {
    path: '/component',
    redirect: '/component/install',
    name: 'form',
    component: {
      render () {
        return (
          <div style="height: 100%;width:100%;display: flex">
            <df-aside width="325px">
              <MenuModel />
            </df-aside>
            <df-main>
              <router-view />
            </df-main>
            <df-backtop
              target=".df-main"
              right={100}
              bottom={150} />
          </div>
        )
      }
    },
    children: [
      {
        path: '/component/install',
        component: Install,
        name: 'default'
      },
      {
        path: '/component/quickStart',
        component: QuickStart,
        name: '快速上手'
      },
      ...baseRouters()
    ]
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  // mode: 'history',
  routes
})

export default router
