<template>
  <div class="df_page_dynamic">
    <df-page-select
      v-model="selectValue"
      :table="table"
      :isPagination="true"
      :pageSize="selectPageSize"
      :valueKey="props.key"
      :querySelectList="getselectlist"
      :selectLabel="props.value"
      @blur="blur"
      @focus="focus"
      @change="change"
    >
      <template v-slot="scope">
        <df-option
          v-for="(item, index) in scope.list"
          :key="item[props.key]"
          :value="item"
          :label="item[props.value]"
        >
        <template v-if="table">
          <!-- 多列 -->
          <df-select-column
            :optionIndex="index"
            :width="col.XIANSHILIEKD * 5"
            v-for="col in scope.column"
            :label="col.XIANSHILIEMC"
            :key="col.XIANSHILIE"
            :prop="col.XIANSHILIE"
          ></df-select-column>
        </template>
        <!-- 单列 -->
        <template v-else/>
        </df-option>
      </template>
    </df-page-select>
  </div>
</template>
<script>
import request from './api/index'
import {getCookie} from './api/lib/cookies'
import dynamicComponent from '../../store/index'
export default {
  name: 'DfPageDynamic',
  data () {
    return {
      table: true,
      selectPageSize: 5,
      list: [],
      page: 1,
      column: [],
      total: 1
    }
  },
  props: {
    peiZhiId: {
      type: String
    },
    fuWuTjMc: {
      type: String
    },
    value: {
      type: Object / String, // eslint-disable-line
      default: () => {}
    },
    props: {
      type: Object,
      required: true
    }
  },
  computed: {
    selectValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
      }
    }
  },
  methods: {
    async getselectlist ({ page, pagesize, keyword }) {
      const pagekeyword = typeof (keyword) === 'object' ? '' : keyword
      // console.log(page)
      await request({
        url: '/winbff-jichufw/DongTaiCx/getShuJuList',
        method: 'post',
        data: [{'peiZhiId': this.peiZhiId, 'fuWuTjMc': this.fuWuTjMc, 'dangQianYe': page, 'chaXunCs': {'SHURUMLX': 'SHURUMA1', 'SHURUMA': pagekeyword, 'JIAGETX': '1', 'YUANQUID': '-9999'}}],
        headers: {
          DangQianYhId: getCookie('DangQianYhId') || 'DBA',
          Authorization: getCookie('Authorization') || 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ5b25nSHVYbSI6Iuezu-e7n-.ZZrjcAEc-djMFTIz3xTmXpYIqyD2T7wx3hZHiGTYI84',
          ...dynamicComponent.getState()
        }
      }).then(res => {
        this.list = res[0].data
        this.column = JSON.parse(res[0].xianShiLieSz)
        this.total = res[0].zongTiaoShu
        this.selectPageSize = res[0].meiYeTs
        if (JSON.parse(res[0].xianShiLieSz).length === 1) {
          this.table = false
        } else {
          this.table = true
        }
      })

      return new Promise((resolve, reject) => {
        // console.log(this.list, this.total, this.column)
        resolve({
          list: this.list,
          total: this.total,
          column: this.column
        })
      }).catch((err) => {
        console.log(err)
      })
    },
    blur (event) {
      this.$emit('blur', event)
    },
    focus (event) {
      this.$emit('focus', event)
    },
    change (event) {
      this.$emit('change', event)
    }
  },
  created () {

  }
}

</script>
