export default {
  state: {
    headers: {
      MAC: '00-01-6C-06-A6-29',
      'x-send': 'httpKey',
      zhiGongXm: encodeURIComponent('系统管理员'),
      yuanQuId: 1
    }
  },
  setHeaders: null,
  getState () {
    this.setState()
    return this.state.headers
  },
  setState () {
    this.state.headers = Object.assign({}, this.state.headers, this.setHeaders())
  },
  create (headers) {
    this.setHeaders = headers
  }
}
