<template lang="html">
  <div class="menu-model">
    <df-menu
      :unique-opened="true"
      :default-openeds="['1', '2', '3']"
      :default-active="defaultActive"
      :router="true"
    >
      <df-submenu index="1">
        <template slot="title">
          <span>开发指南</span>
        </template>
        <df-menu-item-group>
          <df-menu-item index="/component/install">
            安装
          </df-menu-item>
        </df-menu-item-group>
        <df-menu-item-group>
          <df-menu-item index="/component/quickStart">
            快速上手
          </df-menu-item>
        </df-menu-item-group>
      </df-submenu>
      <df-submenu index="2">
        <template slot="title">
          <span>业务组件</span>
        </template>
        <df-menu-item-group
          :title="group.title"
          v-for="group in baseMenu"
          :key="group.title"
        >
          <df-menu-item
            :index="menu.path"
            v-for="menu in group.children"
            :key="menu.path"
          >
            {{ menu.name }}
          </df-menu-item>
        </df-menu-item-group>
      </df-submenu>
    </df-menu>
  </div>
</template>

<script>
import {pages} from '../router'
export default {
  components: {
  },
  data () {
    return {
      defaultActive: '/',
      baseMenu: []
    }
  },
  created () {
    function createMenu (group, page) {
      return {
        title: group,
        children: page.map(item => {
          return {
            name: `${item.path.charAt(0).toUpperCase()}${item.path.slice(1)} ${item.name}`,
            path: `/component/${item.path}`
          }
        })
      }
    }
    const menus = []
    for (const key in pages) {
      if (Object.hasOwnProperty.call(pages, key)) {
        const element = pages[key]
        menus.push(createMenu(key, element))
      }
    }
    this.baseMenu = menus
    const path = this.$route.fullPath
    this.defaultActive = path === '/' ? '/' : path
  },
  methods: {
  }
}
</script>

