// 组件入口文件
import DfPageDynamic from '../packages/page-dynamic/index.js'
import dynamicComponent from '../packages/store/index'
// 存储组件列表
const components = [
  DfPageDynamic
]

// 定义 install 方法，接收 Vue 作为参数。如果使用 use 注册插件，则所有的组件都将被注册
const install = function (Vue, headers) {
  // 遍历注册全局组件
  dynamicComponent.create(headers)
  components.map(component => Vue.component(component.name, component))
  // console.log(dynamicComponent.state)
}

// 判断是否是直接引入文件
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  // 导出的对象必须具有 install，才能被 Vue.use() 方法安装
  install,
  // 以下是具体的组件列表
  ...components
}
